import style from "./AuthReg.module.css"
import { Formik, Form, Field, ErrorMessage } from "formik"
import * as Yup from "yup"
import Logo from "../../Logo"
import axios from "axios"
import { useNavigate } from "react-router"
import { Link } from "react-router-dom"
import { useRef, useState } from "react"
import { useDispatch } from "react-redux"
import { setUser } from "../../../store/slice"
import passNo from "./../../../Images/passNo.png"
import passYes from "./../../../Images/passYes.png"

const URL = "http://92.255.79.239:8000/api/auth/token/login/"

const AuthReg = () => {
  const [reply, setReply] = useState("")
  const [passwordType, setPasswordType] = useState(passNo)
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const passRef = useRef(null)

  const togglePassInput = () => {
    if (passwordType === passNo) {
      passRef.current.type = "text"
      setPasswordType(passYes)
    } else if (passwordType === passYes) {
      passRef.current.type = "password"
      setPasswordType(passNo)
    }
  }
  const registerHandler = async (values, { setSubmitting }) => {
    const payload = {
      username: values.username,
      password: values.password,
    }
    try {
      const response = await axios.post(URL, payload)
      dispatch(
        setUser({
          token: response.data.auth_token,
        })
      )

      response.data.auth_token &&
        setReply(` ${payload.username}     `)

      navigate("/rectangle")
    } catch (e) {
      console.log(e)
      setReply(`    `)
    } finally {
      setSubmitting(false)
    }
  }

  function resetReply() {
    setReply("")
  }
  const validationSchema = Yup.object({
    username: Yup.string()
      .matches(/^[A-Za-z0-9--@.]+$/, "  ")
      .min(6, "    6   ")
      .max(32, "    6  32 ")
      .required(" "),
    // .minLowercase(
    //   1,
    //   "    6  32 ,        "
    // )
    // // .minUppercase(1, "password must contain at least 1 upper case letter")
    // .minNumbers(
    //   1,
    //   "    6  32 ,        "
    // )

    password: Yup.string()
      .matches(/^[A-Za-z0-9--]+$/, "  ")
      .min(6, "  ")
      .max(32, "  ")
      // .minLowercase(
      //   1,
      //   "    6  32 ,      ,     "
      // )
      // .minUppercase(
      //   1,
      //   "    6  32 ,      ,     "
      // )
      .minNumbers(
        1,
        "    6  32 ,        "
      )
      .required(" "),
  })

  return (
    <div className={style.root}>
      <div className={style.formRegist}>
        <div className={style.logo}>
          <Logo />
        </div>
        <div className={style.regist}>
          <h1></h1>
          <Formik
            initialValues={{
              email: "",
              username: "",
              password: "",
            }}
            validateOnBlur
            validationSchema={validationSchema}
            onSubmit={registerHandler}
          >
            {({ isValid, dirty, isSubmiting }) => (
              <Form className={style.form} onChange={resetReply}>
                <br />
                <label></label>
                <Field
                  type="username"
                  name="username"
                  className={style.input}
                  placeholder={" ..."}
                />
                <ErrorMessage
                  name="username"
                  component="div"
                  className={style.error}
                />
                <br />
                <div className={style.password_recovery}>
                  <label></label>
                  <Link to="recovery" className={style.recovery}> ?</Link>
                </div>
                <div className={style.pass}>
                  <Field
                    type="password"
                    name="password"
                    className={style.input}
                    placeholder={" ..."}
                    innerRef={passRef}
                  />
                  <img
                    className={style.icon}
                    src={passwordType}
                    onClick={() => togglePassInput()}
                  ></img>
                  <ErrorMessage
                    name="password"
                    component="div"
                    className={style.error}
                  />
                </div>

                <br />
                <p className={style.textReg}>
                       , <br />
                  <Link to="/login" className={style.reg}>
                    {" "}
                    
                  </Link>
                </p>
                <br />
                <div className={style.only_lat_message}>* </div>
                <div className={style.reply}>{reply}</div>
                <button
                  className={style.btn}
                  type={"submit"}
                  disabled={isSubmiting}
                >
                  
                </button>
              </Form>
            )}
          </Formik>
        </div>
      </div>
      <div className={style.registFon}>
        {/* <div className={style.textFon}>
          <div className={style.welcome}>   FREENANCE</div>
          <div className={style.textConcept}> c !</div>
        </div> */}
      </div>
    </div>
  )
}

export default AuthReg
